	module.exports = {"pathContext":{"docHtml":"<h1>foreach</h1>\n<p>Iterate over the key value pairs of either an array-like object or a dictionary like object.</p>\n<p><a href=\"https://ci.testling.com/manuelstofer/foreach\"><img src=\"https://ci.testling.com/manuelstofer/foreach.png\" alt=\"browser support\"></a></p>\n<h2>API</h2>\n<h3>foreach(object, function, [context])</h3>\n<pre><code class=\"language-js\">var each = require('foreach');\n\neach([1,2,3], function (value, key, array) {\n    // value === 1, 2, 3\n    // key === 0, 1, 2\n    // array === [1, 2, 3]\n});\n\neach({0:1,1:2,2:3}, function (value, key, object) {\n    // value === 1, 2, 3\n    // key === 0, 1, 2\n    // object === {0:1,1:2,2:3}\n});\n</code></pre>","docPath":"argo-ci/node_modules/foreach/readme","proj":"argo-ci"}}

